import React, { useContext, useEffect,useState } from 'react'
import Navbar from '../../../laout/NavbarBack'
import { Typography, Table, Input, Tag, Button, Modal, message, Icon } from 'antd'

import File1 from '../File/File1/File1'
import File2 from '../File/File2/File2'
import FileSearch from '../File/FileSearch/FileSearch'
import qs from 'qs'
import axios from 'axios'
import FileContext from '../../../../context/file/fileContext'






import { Link, Route,useHistory } from 'react-router-dom'  //a


import '../File/File.scss'


const FileBack = (props) => {

    //
    const { Text } = Typography
    const { Search } = Input
    const histroy = useHistory() //history Hook


    //context
    const fileContext = useContext(FileContext)
    const { _getFile, _file,addFileType, getFileType, mode,fileType = [], } = fileContext

    //
    useEffect(() => {
        getFileType(1)
    }, [])

    //    
    const [visible, setVisible] = useState(false)
    const [visible2, setVisible2] = useState(false)

    //
    const [fileTypeName, setFileTypeName] = useState('')

    const handleFileTypeNameChange = (val) => {
        if (val.target.value.length > 10) { return }
        setFileTypeName(val.target.value)
    }

    //
    const showModal = () => {
        setVisible(true)

    };

    const clearName = () => {
        setFileTypeName('')
    }

    const handleOk = e => {
        setVisible(false)
        if (!fileTypeName) {
            message.info('!')
            clearName()
            return
        }

        //
        axios.put('/AutomaticOfficeSystem/fileManagement/api/v1/folders',
            qs.stringify({
                folderName: fileTypeName,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {
                    message.info('~');
                    clearName();
                    setTimeout(() => {
                        getFileType(1)
                    }, 200)
                }
                else {
                    message.info('!');
                    clearName();
                    return
                }
            }, () => {
                return Promise.reject('!')
            }).catch(err => {
                message.info(err)
                clearName()
            })


    };

    const handleCancel = e => {
        setVisible(false)
        clearName()
    };

    const handleSearch = (v) => {
        if (!v) {
            message.info('')
            return
        }
        console.log(v)

        // getSearchTable(v,5,1)
        console.log('before',v)
        _getFile(v,5,1) //val
        setTimeout(() => {
            //
            props.history.push({ pathname: '/fileback/searchFile' ,query: { name : v }})
       
        })


    }


    return (<div >
        <Navbar title="" />
        <div className="container" >
            <div className='File'>
                <div>
                    <Modal
                        title=""
                        visible={visible}
                        onOk={handleOk}
                        onCancel={handleCancel}
                        destroyOnClose={true}
                    >
                        <div className='File-wrap__div'>
                            <Text style={{ fontSize: 'normal' }} className="File-wrap__text"></Text>
                            <Input
                                className="File-wrap__input"
                                placeholder=""
                                value={fileTypeName}
                                onChange={handleFileTypeNameChange}
                            />
                        </div>
                    </Modal>
                </div>
                <div className='File-left' style={{ overflowY: 'scroll' }}>
                    <div className='File-left__item1' >
                        <span></span>     
                        {/* <Text> */}
                          <Icon onClick={showModal} className='File-icon' type="plus-square" />
                        {/* </Text> */}
                    </div>



                    {
                        fileType.length > 0 && fileType.map(e => (
                            <Link
                                className='File-left__item3'
                                key={e.id}
                                to={e.id === 1 ? '/fileback' : `/fileback/file${e.id}`}>
                                <img className='File-left__item3--img'
                                    src={require('../../../../pic/21.png')}
                                    alt='pic'
                                />
                                <Text className='File-left__item3--text'>{e.name}</Text>
                            </Link>
                        ))
                    }


                </div>
                <div className='File-bfc'>
                    <div className='File-right'>
                        {/* <div className='navText' style={{ position: 'relative' }}> */}
                        {!mode && <div className='File-search' >
                            <Text style={{ minWidth: '65px' }}></Text>
                            <Search
                                maxLength={20}
                                placeholder=""
                                onSearch={handleSearch}
                                style={{ width: 200,zIndex:999 }}
                            />
                        </div>}
                        {/* </div> */}
                        {/*  */}

                        <Route path='/fileback' exact component={File1} />
                        <Route path='/fileback/file:param' component={File2} />
                        <Route path='/fileback/searchFile' component={FileSearch} />

                    </div>
                </div>
            </div>
        </div>
    </div>
    )
}

export default React.memo(FileBack)