import React, {
    useState, useEffect
    // , useContext
} from 'react'
import { Typography, Input, Button, DatePicker, message, Select } from 'antd'
import { Link } from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
import '../expenseReq/expenseReq.scss'



const ExpenseReq = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [money, setMoney] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState('')
    const [receip, setReceip] = useState('')
    const [account, setAccount] = useState('')
    const [date, setDate] = useState(null); // reactHook  useStatestate

    const { Text } = Typography
    const { Option } = Select

    //
    useEffect(() => {
        return handleClear
    }, [])
    const handleClear = () => {
        clearData()
    }
    //
    const clearData = () => {
        setName('')
        setTitle('')
        setNotice('')
        setType('')
        setDate(null)
        setAccount('')
        setReceip('')
        setMoney('')
    }
    // 
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleMoneyChange = (val) => {
        if (val.target.value.length > 25) { return }
        setMoney(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if (val.target.value.length > 140) { return }
        setNotice(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleReceiptChange = (val) => {
        if (val.target.value.length > 35) { return }
        setReceip(val.target.value)
    }
    const handleAccountChange = (val) => {
        if (val.target.value.length > 35) { return }
        setAccount(val.target.value)
    }
    const handleDateChange = (val) => {
        setDate(val)
    }
    const handleSubmit = () => {

        if (!username || !title || !money || !notice || !type || !receip || !account || !date) {
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _money = String(money + '')

        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/reimbursementApplication',
            qs.stringify({
                accountNo: account,
                amount: _money,
                invoiceNo: receip,
                name: username,
                note: notice,
                paymentDate: _date,
                paymentWay: type,
                title,

            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {

                    message.info('');
                    clearData();
                    setTimeout(() => {
                        props.history.push('/index/trackProcess')
                    }, 200)

                }
                else {
                    message.info('');
                    clearData();
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                clearData()
            })
    }

    return (
        <div className='expenseReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='expenseReq-wrap'>
                <div className='expenseReq-wrap__title' style={{ marginBottom: '1px' }}></div>
                <div className='expenseReq-wrap__box' style={{ marginBottom: '-20px' }}>
                    <div className='expenseReq-wrap__div' >
                        <Text strong className="expenseReq-wrap__text"></Text>
                        <Input
                            className="expenseReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='expenseReq-wrap__div'>
                        <Text strong className="expenseReq-wrap__text"></Text>
                        <Input
                            className="expenseReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='expenseReq-wrap__box'>
                    <div className='expenseReq-wrap__div'>
                        <Text strong className="expenseReq-wrap__text"></Text>
                        <DatePicker
                            value={date}
                            onChange={handleDateChange}
                            className="expenseReq-wrap__input"></DatePicker>
                    </div>
                    <div className='expenseReq-wrap__div'>
                        <Text strong className="expenseReq-wrap__text"></Text>
                        <Input
                            className="expenseReq-wrap__input"
                            placeholder="()"
                            value={money}
                            type='number'
                            onChange={handleMoneyChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{ marginTop: '15px' }}>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                            value={type}
                        >
                            <Option value=''></Option>
                            <Option value=''></Option>

                        </Select>
                    </div>
                    <div className='vacationReq-wrap__div' >
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={receip}
                            onChange={handleReceiptChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{ marginTop: '15px', width: '50%' }}>
                    <div className='vacationReq-wrap__div' >
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder="/"
                            value={account}
                            onChange={handleAccountChange}
                        />
                    </div>
                </div>


                <div className='expenseReq-wrap__notice'>
                    <Text strong className="expenseReq-wrap__text"></Text>
                    <textarea
                        className="expenseReq-wrap__textarea"
                        value={notice}
                        type='textarea'
                        onChange={handleNoticeChange}
                    />
                </div>

                <div className='expenseReq-wrap__bottom'>
                    <Button type='danger' className='expenseReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess/expense' style={{ height: '100%' }}><Button className='expenseReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default React.memo(ExpenseReq)
