import React, { useState,useEffect
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker, message,Select } from 'antd'
import {Link} from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
import '../checkUnusual/checkUnusual.scss'



const CheckUnusual = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState(''); // reactHook  useStatestate
    const [date, setDate] = useState(null); // reactHook  useStatestate

    //
    useEffect(() => {
        return handleClear
    }, [])
    const handleClear = () => {
        clearData()
    }
    //
    const clearData = () => {
        setName('')
        setTitle('')
        setTime('')
        setNotice('')
        setType('')
        setDate(null)
    }
    const { Text } = Typography
    const { Option } = Select

    // 
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if (val.target.value.length > 5) { return }
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if (val.target.value.length > 140) { return }
        setNotice(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleDateChange = (val)=>{
        setDate(val)
    }
    const handleSubmit = () => {

        if(!username || !date|| !title || !time || !notice || !type ){
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _time = String(time+'')

        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/attendanceAnomalyApplication',
        qs.stringify({
           name:username, 
           title,
           date: _date,
           howLong: _time,
           note:notice,
           exceptionType:type,
        }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Accept': 'application/json;charset=UTF-8'
            }
        }
    ).then(
        (data) => {
            console.log(data)
            if (data.status === 200 || data.status === 201) {

                message.info('');
                clearData();
                setTimeout(() => {
                    props.history.push('/index/trackProcess')
                }, 200)

            }
            else {
                message.info('');
                clearData();
                return
            }
        }, () => {
            return Promise.reject('')
        }).catch(err => {
            message.info(err)
            clearData()
        })
    }

    return (
        <div className='checkUnusual'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='checkUnusual-wrap'>
                <div className='checkUnusual-wrap__title'></div>
                <div className='checkUnusual-wrap__box'>
                    <div className='checkUnusual-wrap__div'>
                        <Text strong className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='checkUnusual-wrap__div'>
                        <Text strong className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='checkUnusual-wrap__box'>
                    <div className='checkUnusual-wrap__div'>
                        <Text strong className="checkUnusual-wrap__text"></Text>
                        <DatePicker 
                           value={date} 
                           onChange={handleDateChange}
                           className="checkUnusual-wrap__input"></DatePicker>
                    </div>
                    <div className='checkUnusual-wrap__div'>
                        <Text strong className="checkUnusual-wrap__text"></Text>
                        <Input
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>

                <div className='checkUnusual-wrap__box' style={{ marginTop: '25px', width: '50%' }}>
                    <div className='checkUnusual-wrap__div'>
                        <Text strong className="checkUnusual-wrap__text"></Text>
                        <Select
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                            value={type}
                        >

                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>

                        </Select>
                    </div>

                </div>


                <div className='checkUnusual-wrap__notice'>
                    <Text strong className="checkUnusual-wrap__text"></Text>
                    <textarea
                        className="checkUnusual-wrap__textarea"
                        value={notice}
                        type='textarea'
                        onChange={handleNoticeChange}
                    />
                </div>

                <div className='checkUnusual-wrap__bottom'>
                    <Button type='danger' className='checkUnusual-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{ height: '100%' }}><Button className='checkUnusual-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default React.memo(CheckUnusual)
