import React, { useEffect,useState,useContext,useRef } from 'react'
import { Typography, Modal, Button,Input,message } from 'antd'
import { Link } from 'react-router-dom'
import Spinner from '../../../../laout/Spinner'
import GroupContext from '../../../../../context/group/groupContext'
import axios from 'axios'
import qs from 'qs'
import '../Department/Department.scss'




const Department = (e) => {

    //
    const { Text } = Typography
    const fileUpload = useRef(null) //
    const [imgUrl,setImgUrl] = useState('')//

    //context
    const groupContext = useContext(GroupContext)
    const { getIcons,depIcon=[], loading } = groupContext
 
    const clearData = ()=>{
        setName('')
        setNotice('')

    }

    useEffect(() => {  //useEffect didMount
        getIcons()
        // eslint-disable-next-line
        return handleWillUnMount
    }, [])

    const [name, setName] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    console.log(depIcon)

    const handleWillUnMount = () =>{
        clearData()
    }

    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }

    const handleUpload = () =>{

        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        //
        if(fileUpload.current.files[0] === undefined){
            message.info('!')
            return
        }

        //formData
        let formData = new FormData()
        formData.append('file',fileUpload.current.files[0])

        axios.post('/AutomaticOfficeSystem/common/api/v1/uploadPortraits', formData,config).then(data => {
            if (data.status === 200) {
               console.log(data.data.data.path)
                // let link = 'http://l1nkkk.xyz:3001'
                // let imgUrl = data.data.img
                let img = data.data.data.path
                setImgUrl(img)
                message.info('~')
            }
            else{
                message.info('');
                return
            }
        }).catch(err => {
            message.ingo(err)
        })
    }

    //    
    const [visible, setVisible] = useState(false)

    const showModal = () => {
        setVisible(true)
        console.log('add')
        console.log(depIcon)
    };
    
 

    const handleOk = e => {
        setVisible(false)
        if(!imgUrl){
            message.info('')
            clearData()
            return
        }

        if (!name) {
            message.info('')
            clearData()
            return
        }
        if (!notice) {
            message.info('')
            clearData()
            return
        }
        if (name.length < 1 || name.length > 30) {
            message.info('')
            clearData()
            return
        }
        if (notice.length < 1 || notice.length > 140) {
            message.info('')
            clearData()
            return
        }

        console.log(imgUrl,name,notice)
        axios.put('/AutomaticOfficeSystem/organizationalManagement/api/v1/department', qs.stringify({ 
            name,
            portraits:imgUrl,
            responsibility:notice
         }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
            }
        }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {

                    message.info('');
                    setTimeout(() => {
                        getIcons()
                    }, 1000)
                }
                else {
                    message.info('');
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
            })
        
        
    };

    const handleCancel = e => {
        setVisible(false)
        clearData()

    };



    return (
        <div className='Department'>
            <div>
                <Modal
                    title=""
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                      <div className='Department-wrap__div' style={{position:'relative'}}>
                        <Text style={{ textAlign:'left',fontSize: 'normal' }} className="Department-wrap__text"></Text>
                        <span><input type="file" className='Department-wrap__input' ref={fileUpload} /></span>
                        <Button className='Department-btn' type='danger' onClick={handleUpload}></Button>

                    </div>
                    {/* <div className='Department-wrap__div'> */}
                        {/* <Text style={{ textAlign:'left',fontSize: 'normal' }} className="Department-wrap__text"></Text> */}
                    {/* </div> */}
                    <div className='Department-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right2-wrap__text"></Text>
                        <Input
                            className="Department-wrap__input"
                            placeholder=""
                            value={name}
                            onChange={handleNameChange}
                        />
                    </div>
                  
                    <div className='Department-wrap__notice'>
                        <Text style={{position:'relative',right:'20px'}} className="Department-wrap__text"></Text>
                        <textarea
                            className="Department-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>
                </Modal>
            </div>
            <div style={{padding:'0 3%'}}>
            <div className='navText' >
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            </div>
            <ul className='Department-ul' style={{padding:'0 3%'}}>
                <li className='Department-ul__li'>
                    <Button onClick={showModal} className='Department-ul__li--link' style={{ fontSize: '15px' }}></Button>
                </li>
            </ul>
            <div className='Department-wrap Department-box'>

                <div style={{padding:'0 3%'}}>
                <div className='navWrap' style={{ padding: '5px 0 0 2px' ,}}>
                    {
                        depIcon.length>0 &&
                        depIcon.map(item=>(
                            <div className='navItem2' key={item.id}>
                            <Link
                                to={`/indexback/department${item.id}`}
                                className='navBox2'>
                                <img
                                    src={item.portraits}
                                    alt='pic'
                                />
                            </Link>
                            <Text className='navItem_text2'>{item.name}</Text>
                        </div>
                          )
                        )
                    }
                </div>
                </div>

            </div>
        </div>


    )
}

export default React.memo(Department)
