    //useReducerstateReducer
    const [state, dispatch] = useReducer(FileReducer, initialState) //1reduver       2 

    //                                                          
    
    //getTable ,
    const getFileType = async (param) => {
        let res = null
        try{
           res  =await axios.get(`/AutomaticOfficeSystem/fileView/api/v1/folders`,{
           },{
               headers:{
                   'content-type':'application/json;charset=UTF-8'
               }
           })
        }catch(err){
            setLoading(false)
            return Promise.reject(err)
        }

        res.data.data.forEach(e=>{ //
            if(e.id === Number(param)){ 
                dispatch({
                    type: GET_FILETYPE,
                    payload: {
                        fileType: res.data.data,
                        title: e.name
                    }
                })
            }
        })
       
    }
    
    //getFile ,
    const getFile = async (folderId,limit,page) => {

        setLoading(true)
        let res = null
        try{
           res  =await axios.get(`/AutomaticOfficeSystem/fileView/api/v1/files?folderId=${folderId}&limit=${limit}&page=${page}`,{
           },{
               headers:{
                   'content-type':'application/json;charset=UTF-8'
               }
           })
        }catch(err){
            setLoading(false)
            // message.info(err)
            return Promise.reject(err)
        }
        dispatch({
            type: GET_FILE,
            payload: {
                file: res.data.data,
                count:res.data.count

            }
        })
        setLoading(false)
        setMode(false)

    }

    
