import React, { useEffect } from 'react'
import PropTypes from 'prop-types'
import { Link, NavLink } from 'react-router-dom'  //a
import { Menu, Dropdown, Icon, message, Popconfirm } from 'antd'
import axios from 'axios'


const Navbar = (props) => {
    const { title } = props
    let name = localStorage.getItem('name')
    let token = localStorage.getItem('token')
    let mess = token ? name + '' : ''

    useEffect(() => {  //useEffect didMount -- 
        // let name = localStorage.getItem('name')
        // let token = localStorage.getItem('token')
        if (!token) {
            message.info('');
            setTimeout(() => {
                // props.history.push({ pathname: '/' })
                window.location.href = '/'
            }, 1000)
        }
        else {
            checkToken()
        }


        //axios
        axios.interceptors.request.use(function (config) {
            // Do something before request is sent
            let token = window.localStorage.getItem("token")
            let name = window.localStorage.getItem("name")

            config.headers.token = token;    //token
            config.headers.username = name;    //token
            return config;
            //tokentokentokenkeytokenkey
            // config.headers['accessToken'] = Token;
        }, function (error) {
            // Do something with request error
            return Promise.reject(error);
        });

        // eslint-disable-next-line
    }, [])

    //token
    const checkToken = async () => {
        axios.post('AutomaticOfficeSystem/login/api/v1/testToken', {},
            {
                headers: {
                    'username': name,
                    'token': token
                }
            }
        ).then((data) => {
            if (data.status === 200) {
                if (data.data.msg === 'ok') {
                    console.log('check', data)
                }

            }
        }, () => {
            return Promise.reject('')
        })
            .catch((err) => {
                console.log(err);
                message.info(err);
                localStorage.removeItem('name')
                localStorage.removeItem('token')
                localStorage.removeItem('mode')

                setTimeout(() => {
                    window.location.href = '/'
                }, 1000)
            });
    }

    const logOut = () => { //token
        localStorage.removeItem('name') //
        localStorage.removeItem('token')
        localStorage.removeItem('mode')

        message.info('');
        setTimeout(() => {
            // props.history.push({ pathname: '/' })
            window.location.href = '/'
        }, 1000)


    }

    const cancel = () => {
        message.info('')
    }

    const menu = (
        <Menu>
            <Menu.Item>

                {token &&
                    <Popconfirm
                        title=""
                        // width={100}
                        style={{ width: '1000px' }}
                        onConfirm={logOut}
                        onCancel={cancel}
                        okText="Yes"
                        cancelText="No"
                    >
                        <span href="#"></span>
                    </Popconfirm>
                }

                {!token && <Link to="/">
                    
                </Link>}
            </Menu.Item>
        </Menu>
    );

    return (
        <nav className="navbar App-header">
            <div className='nav-color'>
                {title}
            </div>
            <ul className="nav_ul">
                <li><NavLink className="link" activeClassName="nav_active" to="/index"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/notice"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/file"></NavLink></li>
                <li><NavLink className="link" activeClassName="nav_active" to="/people"></NavLink></li>
            </ul>
            <ul className='nav_ul2'>
                <Dropdown overlay={menu} className='dropDown'>
                    {/* // eslint-disable-next-line  */}
                    <li className="link"
                        style={{ fontSize: '21px' }}
                    >
                        {mess}
                        <Icon className='navIcon'
                            spin
                            style={{
                                fontWeight: 'bolder', fontSize: '25px'
                                , position: 'relative', top: '2px'
                            }}
                            type="down" />
                    </li>
                </Dropdown>
            </ul>
        </nav>
    )
}

Navbar.defaultProps = {  //
    title: '',
};

Navbar.propTypes = {
    title: PropTypes.string.isRequired,
};
export default Navbar
