import React, { useEffect, useState,useRef } from 'react'
import { Typography, Input, Button,message } from 'antd'
import { Link,useHistory } from 'react-router-dom'
import axios from 'axios'
import qs from 'qs'
import './InfoAdd.scss'




const InfoAdd = (props) => {

   
    //
    const fileUpload = useRef(null) //
    const { Text } = Typography
    const [imgUrl,setImgUrl] = useState('')//
    const histroy = useHistory() //history Hook

    useEffect(() => {  //useEffect didMount
        console.log('InfoAdd-page ')
        // eslint-disable-next-line
    }, [])
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate

    // const { MonthPicker, RangePicker } = DatePicker;

    // 
   
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }


    //
    const handleUpload = () =>{

        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }
        //
        if(fileUpload.current.files[0] === undefined){
            message.info('!')
            return
        }

        //formData
        let formData = new FormData()
        formData.append('file',fileUpload.current.files[0])

        axios.post('/AutomaticOfficeSystem/announcement/api/v1/messagePreview', formData,config).then(data => {
            if (data.status === 200 || data.status === 201) {
               console.log(data.data.data.path)
                // let link = 'http://l1nkkk.xyz:3001'
                // let imgUrl = data.data.img
                let img = data.data.data.path
                setImgUrl(img)
                message.info('~')
            }
            else{
                message.info('');
                return
            }
        }).catch(err => {
            message.ingo(err)
        })
    }
    
    //
    const handleSubmit = () => {

       
        if(!title || !notice){
            message.info('')
            return
        }

        if(!imgUrl){
            message.info('')
            return
        }


        axios.put('/AutomaticOfficeSystem/announcement/api/v1/message', qs.stringify({content:notice,title,preview:imgUrl}),     {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
            }
        }).then(data => {
            if (data.status === 200||data.status === 204 || data.status === 201) {
                message.info('~')
                setTimeout(()=>{

                    histroy.push('/txt/infoManage')
                //    window.location.href = '/txt/infoManage'
                },200)
            }
            else{
                message.info('');
                return
            }
        }).catch(err => {
            message.info(err)
        })

        
    }


    return (
        <div className='InfoAdd'>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <div className='InfoAdd-wrap'>
                <div className='InfoAdd-wrap__box'>
                    <div className='InfoAdd-wrap__div'>
                        <Text  className="InfoAdd-wrap__text"></Text>
                        <Input
                            className="InfoAdd-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
               
                </div>
                
                <div className='InfoAdd-wrap__box'>
                <div className='InfoAdd-wrap__div' style={{position:'relative'}}>
                        <Text  className="InfoAdd-wrap__text"></Text>
                        {/* <Input
                            className="InfoAdd-wrap__input"
                            placeholder=""
                            value={pwd}
                            type='password'
                            onChange={handlePwdChange}
                        /> */}
                        <input type="file" ref={fileUpload} className='InfoAdd-wrap__input'/>
                        <Button className='InfoAdd-btn' type='danger' onClick={handleUpload}></Button>
                    </div>
                   
                </div>

       

                <div className='InfoAdd-wrap__notice'>
                        <Text strong className="InfoAdd-wrap__text"></Text>
                        <textarea
                            className="InfoAdd-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

                <div className='InfoAdd-wrap__bottom'>
                    <Button type='danger' className='InfoAdd-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/txt/infoManage' style={{ height: '100%' }}><Button className='InfoAdd-wrap__bottom--btn' ></Button></Link>
                </div>
            </div>

        </div>
    )
}

export default React.memo(InfoAdd)