import React, {
    useState, useEffect
    // , useContext
} from 'react'
import { Typography, Input, Button, DatePicker, message, Radio, Select } from 'antd'
import { Link } from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
import '../travelReq/travelReq.scss'

const TravelReq = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [radioValue, setRadioValue] = useState(1)
    const [date, setDate] = useState(null); // reactHook  useStatestate

    //
    useEffect(() => {
        return handleClear
    }, [])
    const handleClear = () => {
        clearData()
    }
    //
    const clearData = () => {
        setName('')
        setTitle('')
        setNotice('')
        setDate(null)
        setRadioValue(1)
        setTime('')
    }
    const { Text } = Typography
    // 
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if (val.target.value.length > 5) { return }
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if (val.target.value.length > 140) { return }
        setNotice(val.target.value)
    }
    const handleRadioChange = (val) => {
        setRadioValue(val.target.value)
    }
    const handleDateChange = (val) => {
        setDate(val)
    }
    const handleSubmit = () => {

        if (!username || !title || !time || !notice || !date) {
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _time = String(time + '')
        let _type = radioValue === 1 ? '' : ''
        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/travelApplication',
            qs.stringify({
                howLong: _time,
                name: username,
                note: notice,
                startDate: _date,
                title,
                type: _type,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {

                    message.info('');
                    clearData();
                    setTimeout(() => {
                        props.history.push('/index/trackProcess')
                    }, 200)

                }
                else {
                    message.info('');
                    clearData();
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                clearData()
            })
    }

    return (
        <div className='travelReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='travelReq-wrap'>
                <div className='travelReq-wrap__title'></div>
                <div className='travelReq-wrap__box'>
                    <div className='travelReq-wrap__div'>
                        <Text strong className="travelReq-wrap__text"></Text>
                        <Input
                            className="travelReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='travelReq-wrap__div'>
                        <Text strong className="travelReq-wrap__text"></Text>
                        <Input
                            className="travelReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>

                <div className='travelReq-wrap__box' style={{ width: '50%', marginTop: '25px' }}>
                    <div className='travelReq-wrap__div'>
                        <Text strong className="travelReq-wrap__text"></Text>
                        <Radio.Group onChange={handleRadioChange}
                            value={radioValue}
                            className="travelReq-wrap__input"
                        >
                            <Radio value={1}></Radio>
                            <Radio value={2}></Radio>
                        </Radio.Group>
                    </div>
                </div>
                
                <div className='travelReq-wrap__box'>
                    <div className='travelReq-wrap__div'>
                        <Text strong className="travelReq-wrap__text"></Text>
                        <DatePicker
                            value={date}
                            onChange={handleDateChange}
                            className="travelReq-wrap__input"></DatePicker>
                    </div>
                    <div className='travelReq-wrap__div'>
                        <Text strong className="travelReq-wrap__text"></Text>
                        <Input
                            className="travelReq-wrap__input"
                            placeholder="()"
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>


                <div className='travelReq-wrap__notice'>
                    <Text strong className="travelReq-wrap__text"></Text>
                    <textarea
                        className="travelReq-wrap__textarea"
                        value={notice}
                        type='textarea'
                        onChange={handleNoticeChange}
                    />
                </div>

                <div className='travelReq-wrap__bottom'>
                    <Button type='danger' className='travelReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess/personnel' style={{ height: '100%' }}><Button className='travelReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default React.memo(TravelReq)
