import React, { useState, useEffect, useContext, } from 'react'
import { Typography, Table, Input, Tag, Button, Modal, message, Select } from 'antd'
import moment from 'moment'

import MainContext from '../../../../../../context/main/mainContext'

import '../ApplyCancel/ApplyCancel.scss'
import { Link, Route,useHistory } from 'react-router-dom'  //a


const ApplyCancel = (e) => {


    //
    const { Text } = Typography
    //context
    const mainContext = useContext(MainContext)
    const {clearApply,getApply,getType,apply=[],applyCount=0,applyType=[],loading } = mainContext

    //
    useEffect(()=>{
       getApply('2') //
       getType()

       return handleClear
    },[])

    const handleClear  = () =>{
        clearApply()
        clearRes()
    }

    console.log('',apply,applyCount)
    console.log(':',applyType)



    
     //
     const [visible, setVisible] = useState(false)
 
     //
     const [resName, setResName] = useState('') //
     const [resInfo, setResInfo] = useState(''); //
     
     //
     const clearRes = () =>{
         setResName('')
         setResInfo('')
     } 
     //
     const showModal = (id) => {
         setVisible(true)

        apply.forEach(e=>{
            if(e.id === id){
                setResName(e.processor)
                setResInfo(e.reject)

            }
        })
         
     };
 
 
     const handleOk = e => {
         setVisible(false)
         clearRes()
     };
 
     const handleCancel = e => {
         setVisible(false)
         clearRes()
     };
    
    // - table 
     const [currentPage, setCurrentPage] = useState(1)
     const [currentTotal,setCurrentTotal] = useState(0)
     const paginationProps = {
         showSizeChanger: false,
         showQuickJumper: false,
         showTotal: () => `${data.length>0 && data.length}`,
         pageSize: 5,
         current: currentPage,
         total: applyCount,
         onShowSizeChange: (current, pageSize) => changePageSize(pageSize, current),
         onChange: (current) => changePage(current),
     }
 
     const changePageSize=(a,b)=>{
         console.log(a,b)
     }
 
     const changePage=(v)=>{
         console.log(v)
         setCurrentPage(v)
     }
 
 
    
 
 
 
 
    
     const columns = [
        {
            title: '',
            dataIndex: 'name',
            key: 'name',
            render: text => <span>{text}</span>,
        },
        {
            title: '',
            dataIndex: 'type',
            key: 'type',
        },
        {
            title: '',
            dataIndex: 'date',
            key:'date'
        },
        {
           title: '',
           dataIndex: 'status',
           key:'status'
        },
        {
            title: '',
            dataIndex: 'processor',
            key:'processor'
         },
         {
            title: '',
            dataIndex: 'response',
            key:'response'
         },
        {
            title: '',
            key: 'action',
            render: (text, record) => (
                <span>
                    <span onClick={() => showModal(record.key)} className='redLink' style={{ color: 'red', position: 'relative', left: '10px' }}></span>

                </span>
            ),
        },
    ];

    const data = []; 
    if ( applyType.length >0) {
        apply.forEach((item, index) => {
                data.push({
                    key:item.id,
                    name:item.username?item.username:'',
                    type:item.type?applyType[item.type-1].type_name:'',
                    date:item.date?moment(item.date).format('YYYY-MM-DD'):'',
                    status:item.status===-1 && '',
                    processor:item.processor?item.processor:'',
                    response:item.reject?(item.reject.length>15?item.reject.slice(0,15)+'...':item.reject):''
                    

                })
   
        })
    }
    
    return (
        <div className='ApplyCancel'>
            <div>
                <Modal
                    title=""
                    visible={visible}
                    onOk={handleOk}
                    onCancel={handleCancel}
                    destroyOnClose={true}
                >
                    <div className='ApplyCancel-wrap__div'>
                        <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text"></Text>
                        <Input
                            className="ApplyCancel-wrap__input"
                            placeholder=""
                            value={resName}
                            disabled={true}

                            
                        />
                    </div>
                    <div className='ApplyCancel-wrap__div' style={{marginTop:'17px'}}>
                        <Text style={{ fontSize: 'normal' }} className="Right1-wrap__text"></Text>
                        <textarea
                            className="ApplyCancel-wrap__textarea"
                            value={resInfo}
                            type='textarea'
                            disabled={true}
                        />
                    </div>



                </Modal>
            </div>

           <div className='ApplyCancel-box' style={{marginBottom:'40px'}}>
           <div className='navText'><Text strong className='navFont'></Text></div>
            <div className='navFinish' >
                {/* <Text></Text> */}
                <Text><span style={{color:'red'}}>{applyCount}</span></Text>
                
                <div style={{display:'flex',flexDirection:'column'}}>
                <Link to='/index/trackProcess' className='ApplyCancel-link' ><Button className='ApplyCancel-btn' type='danger'></Button></Link>
                    </div>

                {/*  */}
            </div>
           </div>

           <div className='ApplyCancel-box' >
           <Table columns={columns} dataSource={data} pagination={paginationProps} />
           </div>
           

        </div>
    )
}

export default React.memo(ApplyCancel)
