import React, { useState, useContext, useEffect } from 'react'
import { Button, Input, Typography, message,Icon } from 'antd'
import axios from 'axios'
import AlertContext from '../../context/alert/alertContext'
import qs from 'qs'

const Login = (props) => {

    const alertContext = useContext(AlertContext) //contextstate 
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [password, setPass] = useState(''); // reactHook  useStatestate
    const { Text } = Typography


    useEffect(() => {  //useEffect didMount -- 
        let name = localStorage.getItem('name')
        let token = localStorage.getItem('token')
        let mode = localStorage.getItem('mode')

        console.log(name, token)
        if (token) {
            if(mode === '1'){
                props.history.replace({ pathname: '/index' })
                alertContext.setAlert(`, ${name}`, "dark") //
            }
            else if (mode === '2'){
                props.history.replace({ pathname: '/indexBack' })
                alertContext.setAlert(`, ${name}`, "dark") //
            }
           
        }
        // eslint-disable-next-line
    }, [])

    const clearData = ()=>{
        setName('')
        setPass('')
    }

    // 
    const handleGoLoginBack = ()=>{
        props.history.replace('/loginBack')
    }

    // 
    const handleLoginFront =  (e) => {
        if (!username || !password) {
            message.info('');
            return
        };
        let dt = { password: password, username: username } //qs

        // eslint-disable-next-line
        axios.post('AutomaticOfficeSystem/login/api/v1/login', qs.stringify(dt),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then((data) => {
            if (data.status === 200) {
                if(data.data.msg === 'ok'){
                    console.log(data.data)
                    localStorage.setItem('name', username) //
                    localStorage.setItem('token', data.data.data.token) //
                    localStorage.setItem('mode', '1') //1

                    props.history.push({ pathname: '/index' })
                    alertContext.setAlert("", "dark") 

                }

            }
        },()=>{
            return Promise.reject('')
        })
            .catch((err) => { console.log(err);message.info(err); clearData()});
           

    }

    // 
    const handleNameChange = (val) => {
        setName(val.target.value)
    }
    const handlePassChange = (val) => {
        setPass(val.target.value)
    }


    return (
        <div className="page-login"
            style={{ backgroundSize: 'cover', width: '100%', height: '100%', backgroundImage: "url(" + require("../../pic/29.jpg") + ")" }}
        >
            {/* <Logo/> */}
            <div className='login-wrap'>
                <div><Text strong className='linkFont'></Text></div>
                <span className='rootFont' ></span>
                <Input
                    className='login-name loginInp'
                    value={username}
                    placeholder=""
                    onChange={handleNameChange}
                />
                <span className="login-span rootFont"></span>
                <Input
                    className="loginInp"
                    type="password"
                    placeholder=""
                    value={password}
                    onChange={handlePassChange}
                />

                <div className='login-btn'><Button type="primary"
                    onClick={handleLoginFront}
                ></Button></div>
                 <div className='hover-btn'><Icon type='login' className='login-icon' /><span className='loginFont'
                    onClick={handleGoLoginBack}
                ></span></div>
            </div>
        </div>
    )
}






export default Login

