import {GET_GROUP,GET_SEARCH_TABLE,SET_MODE,GET_DUTY, GET_TABLE,GET_DEPARTMENT,GET_LOGINUSER, ClEAR_USER, SET_LOADING } from '../type'

export default (state, action) => {
    const { type, payload = {} } = action
    const { group,groupCount,val,mode,_table,_count,duty,loading, list, department,table,count } = payload

    switch (type) {
        case SET_LOADING:
            return {
                ...state, //state
                loading: loading
            };
        case SET_MODE:
                return {
                    ...state, //state
                    mode: mode
                };      
        case ClEAR_USER:
            return {
                ...state,
                loginUser: {}
            };
        case GET_LOGINUSER:
            return {
                ...state,
                list: list,
            };
        case GET_DEPARTMENT:
            return {
                ...state,
                department: department
            }    
        case GET_TABLE:
            return{
                ...state,
                table: table,
                count: count,
            }
       case GET_SEARCH_TABLE:
            return {
                ...state,
                _table: _table,
                _count: _count,
                val: val,   //
            }          
        case GET_DUTY:
            return {
                ...state,
                duty: duty,
            }    
        case GET_GROUP:
            return {
                ...state,
                group: group, //
                groupCount: groupCount
            }
        default:
            return state;
    }

}