import React, { useState,useEffect
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker, message,Select } from 'antd'
import {Link} from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'

import '../vacationReq/vacationReq.scss'



const VacationReq = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [time, setTime] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState(''); // reactHook  useStatestate
    const [work, setWork] = useState(''); // reactHook  useStatestate
    const [date, setDate] = useState(null); // reactHook  useStatestate
   
    //
    useEffect(()=>{
        return handleClear
    },[])
    const handleClear = () =>{
        clearData()
    }
    //
    const clearData = () =>{
        setName('')
        setTitle('')
        setTime('')
        setNotice('')
        setType('')
        setWork('')
        setDate(null)
    }

    // const { MonthPicker, RangePicker } = DatePicker;
    const { Option } = Select
    const { Text } = Typography
    // 
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if(val.target.value.length>30){return}
        setName(val.target.value)
    }
    const handleTimeChange = (val) => {
        if(val.target.value.length>5){return}
        setTime(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleWorkChange = (val) => {
        if(val.target.value.length>25){return}
        setWork(val.target.value)
    }
    const handleDateChange = (val)=>{
        setDate(val)
    }
    const handleSubmit = () => {

        if(!username || !date|| !title || !time || !notice || !type || !work){
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _time = String(time+'')
        console.log( username , _date,  title , _time , notice , type , work)

        console.log(typeof username ,typeof _date, typeof title ,typeof _time ,typeof notice ,typeof type ,typeof work)

        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/vacationApplication',
        qs.stringify({
           name:username, 
           title,
           startData: _date,
           howLong: _time,
           reason: notice,
           department:work,
           type,
        }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Accept': 'application/json;charset=UTF-8'
            }
        }
    ).then(
        (data) => {
            console.log(data)
            if (data.status === 200 || data.status === 201) {

                message.info('');
                clearData();
                setTimeout(() => {
                    props.history.push('/index/trackProcess')
                }, 200)

            }
            else {
                message.info('');
                clearData();
                return
            }
        }, () => {
            return Promise.reject('')
        }).catch(err => {
            message.info(err)
            clearData()
        })

    }



    return (
        <div className='vacationReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>

            <div className='vacationReq-wrap'>
                <div className='vacationReq-wrap__title'></div>
                <div className='vacationReq-wrap__box'>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='vacationReq-wrap__box'>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <DatePicker 
                           value={date} 
                           onChange={handleDateChange}
                           className="vacationReq-wrap__input"></DatePicker>
                        </div>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={time}
                            type='number'
                            onChange={handleTimeChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{marginTop:'25px'}}>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                            value={type}
                        >

                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>

                        </Select>
                    </div>
                    <div className='vacationReq-wrap__div' >
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={work}
                            onChange={handleWorkChange}
                        />
                    </div>
                </div>

                    
                    <div className='vacationReq-wrap__notice'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <textarea
                            className="vacationReq-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

               
                <div className='vacationReq-wrap__bottom'>
                    <Button type='danger' className='vacationReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{height:'100%'}}><Button className='vacationReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>
                

        </div>
    )
}

export default React.memo(VacationReq)
