import React, { useReducer } from 'react';
import RightContext from './rightContext';
import RightReducer from './rightReducer';
import axios from 'axios'
import qs from 'qs'
import { message } from 'antd'

import {GET_PIC,CLEAR_MESS,GET_MESS,CLEAR_DATA,GET_DEPARTMENT,GET_DUTY,GET_RIGHT, SET_LOADING } from '../type'



const RightState = props => {
    //
    const initialState = {

        //
        right: [],  //
        loading: false,
        rightCount: 0,
        duty: [],
        department: [],
        pic: [],

        //
        mess: [],  //
        messCount: 0,
        messTitle:'',
        messInfo:'',//
        


    };

    //useReducerstateReducer
    const [state, dispatch] = useReducer(RightReducer, initialState) //1reduver       2 

    //                                                          
      //getRight ,
    const getRight = async (permission) => {
    
        setLoading(true)

        let res = null
        try{
           res  =await axios.get(`/AutomaticOfficeSystem/approvalManagement/api/v1/approvalList?permission=${permission}`,{
           },{
               headers:{
                   'content-type':'application/json;charset=UTF-8'
               }
           })
        }catch(err){
            setLoading(false)
            // message.info(err)
            return Promise.reject(err)
        }
        dispatch({
            type: GET_RIGHT,
            payload: {
                right: res.data.data,
                rightCount: res.data.data.length

            }
        })
        setLoading(false)

    }

      //getMess ,
      const getMess = async (_id) => {
    
        setLoading(true)

        let res = null
        try{
           res  =await axios.get(`/AutomaticOfficeSystem/announcement/api/v1/message`,{
           },{
               headers:{
                   'content-type':'application/json;charset=UTF-8'
               }
           })
        }catch(err){
            setLoading(false)
            return Promise.reject(err)
        }
        if(!_id){
            dispatch({
                type: GET_MESS,
                payload: {
                    mess: res.data.data,
                    messCount: res.data.data.length,
                    messTitle: '',
                    messInfo: '',
                }
            })
        }
        else{
            res.data.data.forEach(e=>{
                if(e.id === Number(_id)){
                    dispatch({
                        type: GET_MESS,
                        payload: {
                            mess: res.data.data,
                            messCount: res.data.data.length,
                            messTitle: e.title,
                            messInfo: e.content
                        }
                    })
                }
            })
        }
      
        setLoading(false)

    }

     //getMess ,
     const getPic = async () => {
    
        setLoading(true)

        let res = null
        try{
           res  =await axios.get(`/AutomaticOfficeSystem/message/api/v1/shufflingFigure`,{
           },{
               headers:{
                   'content-type':'application/json;charset=UTF-8'
               }
           })
        }catch(err){
            setLoading(false)
            return Promise.reject(err)
        }
        dispatch({
            type: GET_PIC,
            payload: {
                pic: res.data.data,

            }
        })
        setLoading(false)

    }
     
    //getDepartment,
    const getDutyList = async () => { //loginuser

        // setLoading(true)//loading  
        let res = null
        try {
            res = await axios.get('/AutomaticOfficeSystem/common/api/v1/getDutyList', {})
        } catch (err) {
            // setLoading(false)
            message.info(err)
            return
        }
        
        dispatch({
            type: GET_DUTY,
            payload: {
                duty: res.data.data
            }
        })
        // setLoading(false)
    };

    //getDepartment,
    const getDepartment = async () => { //loginuser

        // setLoading(true)//loading  
        let res = null
        try {
            res = await axios.get('/AutomaticOfficeSystem/common/api/v1/getDepartmentList', {})
        } catch (err) {
            // setLoading(false)
            message.info(err)
            return
        }
        
        dispatch({
            type: GET_DEPARTMENT,
            payload: {
                department: res.data.data
            }
        })
        // setLoading(false)
    };

   

    //set loading
    const setLoading = (loading) => {
        dispatch({
            type: SET_LOADING, //dispatchReducer
            payload: { loading }
        })
    }

    const clearData = () => {
        dispatch({
            type: CLEAR_DATA,
        })
    }

    const clearMess = () =>{
        dispatch({
            type: CLEAR_MESS
        })
    }
   

    return (<RightContext.Provider value={{
        right: state.right,
        loading: state.loading,
        rightCount: state.rightCount,
        duty: state.duty,
        department: state.department,
        mess: state.mess,
        messCount: state.messCount,
        pic: state.pic,//state
        messTitle: state.messTitle,
        messInfo: state.messInfo,

        getDepartment, //
        getDutyList,
        getMess,
        getPic,
        clearData,
        getRight,
        setLoading,
        clearMess,
    }}>{props.children}</RightContext.Provider>)


}
export default RightState
