import React, { useEffect, useState, useContext, useRef } from 'react'
import { Typography, Input, Button, Select, message, Icon, Upload } from 'antd'
import { Link } from 'react-router-dom'
import Spinner from '../../../../../../component/laout/Spinner'
import axios from 'axios'
import qs from 'qs'

import '../AccountEdit/AccountEdit.scss'




const AccountEdit = (props) => {

    //
    const { Text } = Typography


    const fileUpload = useRef(null)




    //,,
    const { name: name_api = '', email: mail_api = '', tel: phone_api = '', sex: sex_api = '0' } = props.location.query ? props.location.query : {}


    const [username, setName] = useState(name_api ? name_api : ''); // reactHook  useStatestate
    const [type, setType] = useState('0'); // reactHook  useStatestate
    const [phone, setPhone] = useState(phone_api ? phone_api : ''); // reactHook  useStatestate
    const [mail, setMail] = useState(mail_api ? mail_api : ''); // reactHook  useStatestate
    const { Option } = Select
    const reg = /^([a-zA-Z\d])(\w|\-)+@[a-zA-Z\d]+\.[a-zA-Z]{2,4}$/ //

    //
    useEffect(() => {  //useEffect didMount
        if (props.location.query) {
            console.log('set here')
            setType(props.location.query.sex)
        }
    }, [])

    // 
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 20) { return }
        setName(val.target.value)
    }
    const handlePhoneChange = (val) => {
        if (val.target.value.length > 35) { return }
        setPhone(val.target.value)
    }
    const handleMailChange = (val) => {
        if (val.target.value.length > 35) { return }
        setMail(val.target.value)
    }

    const clearData = () => {
        setName('')
        setType('0')
        setMail('')
        setPhone('')
    }
    const handleSubmit = () => {
        if (!type || !username || !mail || !phone || type === '0') {
            message.info('')
            clearData()
            return
        }
        if (username.length < 2 || username.length > 35) {
            message.info('2-35')
            clearData()
            return
        }
        if (!reg.test(mail)) {
            message.info('')
            clearData()
            return
        }
        if (mail.length < 8 || mail.length > 35) {
            message.info('8-35')
            clearData()
            return
        }
        if (phone.length < 8 || phone.length > 35) {
            message.info('8-35')
            clearData()
            return
        }
        let _phone = String(phone)


        console.log('type', type, typeof type)
        console.log('username', username, typeof username)
        console.log('mail', mail, typeof mail)
        console.log('phone', phone, typeof phone)

        axios.patch('/AutomaticOfficeSystem/personnelPortal/api/v1/userInfo', qs.stringify({ name: username, sex: type, email: mail, tel: _phone }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Accept': 'application/json;charset=UTF-8'
            }
        }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 204) {

                    message.info('');
                    clearData();
                    setTimeout(() => {
                        props.history.push({ pathname: '/people' })
                    }, 1000)
                }
                else {
                    message.info('');
                    clearData();
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                console.log(err)
                clearData()
            })

    }
    const updateImg = (portraits) => {
        axios.patch('/AutomaticOfficeSystem/personnelPortal/api/v1/userInfo', qs.stringify({ portraits }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Accept': 'application/json;charset=UTF-8'
            }
        }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 204) {

                    message.info('');
                    setTimeout(() => {
                        props.history.push({ pathname: '/people' })
                    }, 1000)
                }
                else {
                    message.info('');
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
            })
    }

    const handleSubmit2 = () => {
        let config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        }

        if (fileUpload.current.files[0] === undefined) {
            message.info('')
            return
        }
        let formData = new FormData()
        formData.append('file', fileUpload.current.files[0])


        axios.post('/AutomaticOfficeSystem/common/api/v1/uploadPortraits', formData, config).then(data => {
            if (data.status === 200) {
               console.log(data.data.data.path)
                // let link = 'http://l1nkkk.xyz:3001'
                // let imgUrl = data.data.img
                let img = data.data.data.path
                return updateImg(img)

            }
            else{
                message.info('');
                return
            }
        }).catch(err => {
            message.ingo(err)
        })


    }



    return (
        <div className='AccountEdit'>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
                <Link to='/people' className='AccountEdit-link' ><Button className='AccountEdit-btn' type='danger'></Button></Link>
            </div>
            <div className='AccountEdit-wrap'>
                <div className='AccountEdit-wrap__box'>
                    <div className='AccountEdit-wrap__div'>
                        <Text className="AccountEdit-wrap__text"></Text>
                        <Input
                            className="AccountEdit-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                    <div className='checkUnusual-wrap__div'>
                        <Text className="checkUnusual-wrap__text"></Text>
                        <Select
                            className="checkUnusual-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                            defaultValue={type}
                            value={type}
                        >
                            <Option value='0'></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                        </Select>
                    </div>
                </div>
                
                <div className='AccountEdit-wrap__box'>
                    <div className='AccountEdit-wrap__div'>
                        <Text className="Account-wrap__text"></Text>
                        <Input
                            className="AccountEdit-wrap__input"
                            placeholder=""
                            value={mail}
                            onChange={handleMailChange}
                        />
                    </div>
                    <div className='AccountEdit-wrap__div'>
                        <Text className="Account-wrap__text"></Text>
                        <Input
                            className="AccountEdit-wrap__input"
                            placeholder="/"
                            value={phone}
                            type='number'
                            onChange={handlePhoneChange}
                        />
                    </div>
                </div>

                <div className='AccountEdit-wrap__bottom'>
                    <Button className='AccountEdit-wrap__bottom--btn' onClick={handleSubmit}></Button>
                </div>
            </div>
            <div className='navText'>
                <Text strong style={{ color: '#ff0000' }} className='navFont'></Text>
            </div>
            <div className='AccountEdit-wrap__box' style={{ width: '50%', marginTop: '25px' }}>
                <div className='AccountEdit-wrap__div' style={{ position: 'relative', top: '50px', left: '7%' }}>
                    <Text className="AccountEdit-wrap__text"></Text>
                    <input type="file" ref={fileUpload} />
                </div>
            </div>
            <div className='AccountEdit-wrap__bottom'>
                <Button className='AccountEdit-wrap__bottom--btn' onClick={handleSubmit2}></Button>
            </div>
        </div>
    )
}

export default React.memo(AccountEdit)



