import React, { useState,useEffect
    // , useContext
 } from 'react'
import { Typography, Input, Button, DatePicker, message,Select } from 'antd'
import {Link} from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
import '../sealReq/sealReq.scss'



const SealReq = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [phone, setPhone] = useState(''); // reactHook  useStatestate
    const [notice, setNotice] = useState(''); // reactHook  useStatestate
    const [type1, setType1] = useState('')
    const [type2, setType2] = useState('')
    const [date, setDate] = useState(null); // reactHook  useStatestate
   
    //
    useEffect(()=>{
        return handleClear
    },[])
    const handleClear = () =>{
        clearData()
    }
    //
    const clearData = () =>{
        setName('')
        setTitle('')
        setPhone('')
        setNotice('')
        setType1('')
        setType2('')
        setDate(null)
    }

    const { Text } = Typography
    const { Option } = Select

    // 
    const handleTitleChange = (val) => {
        if(val.target.value.length>30){return}
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if(val.target.value.length>30){return}
        setName(val.target.value)
    }
    const handlePhoneChange = (val) => {
        if(val.target.value.length>25){return}
        setPhone(val.target.value)
    }
    const handleNoticeChange = (val) => {
        if(val.target.value.length>140){return}
        setNotice(val.target.value)
    }
    const handleType1Change = (val) => {
       setType1(val)
    }
    const handleType2Change = (val) => {
        setType2(val)
    }
    
    const handleSubmit = () => {
        if(!username || !type1 || !type2 || !date || !notice ||!phone ||!title){
           
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _tel= String(phone)

        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/sealApplication',
        qs.stringify({
           name:username, 
           title,
           tel: _tel,
           note: notice,
           useType:type1,
           sealType: type2,
           useDate: _date
        }),
        {
            headers: {
                'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                'Accept': 'application/json;charset=UTF-8'
            }
        }
    ).then(
        (data) => {
            console.log(data)
            if (data.status === 200 || data.status === 201) {

                message.info('');
                clearData();
                setTimeout(() => {
                    props.history.push('/index/trackProcess')
                }, 200)

            }
            else {
                message.info('');
                clearData();
                return
            }
        }, () => {
            return Promise.reject('')
        }).catch(err => {
            message.info(err)
            clearData()
        })
    }

    const handleDateChange = (val)=>{
        setDate(val)
    }

    return (
        <div className='sealReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='sealReq-wrap'>
                <div className='sealReq-wrap__title'></div>
                <div className='sealReq-wrap__box'>
                    <div className='sealReq-wrap__div'>
                        <Text strong className="sealReq-wrap__text"></Text>
                        <Input
                            className="sealReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='sealReq-wrap__div'>
                        <Text strong className="sealReq-wrap__text"></Text>
                        <Input
                            className="sealReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='sealReq-wrap__box'>
                    <div className='sealReq-wrap__div'>
                        <Text strong className="sealReq-wrap__text"></Text>
                        <DatePicker 
                            value={date} 
                            onChange={handleDateChange}
                            className="sealReq-wrap__input"></DatePicker>
                        </div>
                    <div className='sealReq-wrap__div'>
                        <Text strong className="sealReq-wrap__text"></Text>
                        <Input
                            className="sealReq-wrap__input"
                            placeholder=""
                            value={phone}
                            type='number'
                            onChange={handlePhoneChange}
                        />
                    </div>
                </div>

                <div className='vacationReq-wrap__box' style={{marginTop:'25px'}}>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleType1Change}
                            value={type1}
                        >
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                        </Select>
                    </div>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleType2Change}
                            value={type2}
                        >
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                        </Select>
                    </div>
                </div>


                    
                    <div className='sealReq-wrap__notice'>
                        <Text strong className="sealReq-wrap__text"></Text>
                        <textarea
                            className="sealReq-wrap__textarea"
                            value={notice}
                            type='textarea'
                            onChange={handleNoticeChange}
                        />
                    </div>

                <div className='sealReq-wrap__bottom'>
                    <Button type='danger' className='sealReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess' style={{height:'100%'}}><Button className='sealReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default React.memo(SealReq)
