import React, {
    useState, useEffect
    // , useContext
} from 'react'
import { Typography, Input, Button, DatePicker, message, Select } from 'antd'
import { Link } from 'react-router-dom'
import qs from 'qs'
import axios from 'axios'
import moment from 'moment'
import '../pactReq/pactReq.scss'



const PactReq = (props) => {
    const [username, setName] = useState(''); // reactHook  useStatestate
    const [title, setTitle] = useState(''); // reactHook  useStatestate
    const [number, setNumber] = useState(''); // reactHook  useStatestate
    const [type, setType] = useState(''); // reactHook  useStatestate
    const [pact, setPact] = useState(''); // reactHook  useStatestate
    const [bank, setBank] = useState(''); // reactHook  useStatestate
    const [money, setMoney] = useState(''); // reactHook  useStatestate
    const [date, setDate] = useState(null); // reactHook  useStatestate


    const { Text } = Typography
    const { Option } = Select

    //
    useEffect(() => {
        return handleClear
    }, [])
    const handleClear = () => {
        clearData()
    }
    //
    const clearData = () => {
        setName('')
        setTitle('')
        setNumber('')
        setType('')
        setPact('')
        setBank('')
        setMoney('')
        setDate(null)
    }
    // 
    const handleTitleChange = (val) => {
        if (val.target.value.length > 30) { return }
        setTitle(val.target.value)
    }
    const handleNameChange = (val) => {
        if (val.target.value.length > 30) { return }
        setName(val.target.value)
    }
    const handleNumberChange = (val) => {
        if (val.target.value.length > 25) { return }
        setNumber(val.target.value)
    }
    const handlePactChange = (val) => {
        if (val.target.value.length > 25) { return }
        setPact(val.target.value)
    }
    const handleBankChange = (val) => {
        if (val.target.value.length > 35) { return }
        setBank(val.target.value)
    }
    const handleMoneyChange = (val) => {
        if (val.target.value.length > 25) { return }
        setMoney(val.target.value)
    }
    const handleTypeChange = (val) => {
        setType(val)
    }
    const handleDateChange = (val) => {
        setDate(val)

    }
    const handleSubmit = () => {

        if (!username || !title || !number || !type || !pact || !bank || !money || !date) {
            message.info('')
            clearData()
            return
        }

        let _date = String(date.format('YYYY-MM-DD HH:mm:ss'))
        let _money = String(money + '')
        let _number = String(number)

        //
        axios.put('/AutomaticOfficeSystem/processCenter/api/v1/contractPaymentApplication',
            qs.stringify({
             amount:_money,
             bankcardNo:bank,
             contractId:_number,
             contractName:pact,
             name:username,
             paymentDate:_date,
             paymentTerm:type,
             title,
            }),
            {
                headers: {
                    'content-type': 'application/x-www-form-urlencoded;charset=UTF-8',
                    'Accept': 'application/json;charset=UTF-8'
                }
            }
        ).then(
            (data) => {
                console.log(data)
                if (data.status === 200 || data.status === 201) {

                    message.info('');
                    clearData();
                    setTimeout(() => {
                        props.history.push('/index/trackProcess')
                    }, 200)

                }
                else {
                    message.info('');
                    clearData();
                    return
                }
            }, () => {
                return Promise.reject('')
            }).catch(err => {
                message.info(err)
                clearData()
            })

    }

    return (
        <div className='pactReq'>
            <div className='navText'>
                <Text strong className='navFont'></Text>
            </div>
            <div className='pactReq-wrap'>
                <div className='pactReq-wrap__title'></div>
                <div className='pactReq-wrap__box'>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <Input
                            className="pactReq-wrap__input"
                            placeholder=""
                            value={title}
                            onChange={handleTitleChange}
                        />
                    </div>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <Input
                            className="pactReq-wrap__input"
                            placeholder=""
                            value={username}
                            onChange={handleNameChange}
                        />
                    </div>
                </div>
                
                <div className='pactReq-wrap__box'>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <DatePicker
                            value={date}
                            onChange={handleDateChange}
                            onChange={handleDateChange} className="pactReq-wrap__input"></DatePicker>
                    </div>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <Input
                            className="pactReq-wrap__input"
                            placeholder=""
                            value={number}
                            type='number'
                            onChange={handleNumberChange}
                        />
                    </div>
                </div>


                <div className='vacationReq-wrap__box' style={{ marginTop: '25px' }}>
                    <div className='vacationReq-wrap__div'>
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Select
                            className="vacationReq-wrap__input"
                            placeholder=""
                            onChange={handleTypeChange}
                            value={type}
                        >

                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>
                            <Option value=''></Option>

                        </Select>
                    </div>
                    <div className='vacationReq-wrap__div' >
                        <Text strong className="vacationReq-wrap__text"></Text>
                        <Input
                            className="vacationReq-wrap__input"
                            placeholder=""
                            value={pact}
                            onChange={handlePactChange}
                        />
                    </div>
                </div>

                <div className='pactReq-wrap__box' style={{ marginTop: '25px' }}>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <Input
                            className="pactReq-wrap__input"
                            placeholder=""
                            value={bank}
                            type='number'
                            onChange={handleBankChange}
                        />
                    </div>
                    <div className='pactReq-wrap__div'>
                        <Text strong className="pactReq-wrap__text"></Text>
                        <Input
                            className="pactReq-wrap__input"
                            placeholder=""
                            value={money}
                            type='number'
                            onChange={handleMoneyChange}
                        />
                    </div>
                </div>

                <div className='pactReq-wrap__bottom'>
                    <Button type='danger' className='pactReq-wrap__bottom--btn' onClick={handleSubmit}></Button>
                    <Link to='/index/requestProcess/expense' style={{ height: '100%' }}><Button className='pactReq-wrap__bottom--btn' ></Button></Link>
                </div>

            </div>



        </div>
    )
}

export default React.memo(PactReq)
